import axios from 'axios'

export const Weibo = axios.create({
  baseURL: 'https://api.github.com/',
  headers: {
    'Accept': 'application/json',
    Authorization: `bearer ${window.WEIBO.token.join('')}`
  }
})

export const Comment = axios.create({
  baseURL: 'https://api.github.com/',
  headers: {
    'Accept': 'application/json',
    Authorization: `bearer ${window.COMMENT.token.join('')}`
  }
})

export const RequestByToken = token => {
  if(!token){
    return Promise.reject()
  }
  return axios.create({
    baseURL: 'https://api.github.com/',
    headers: {
      'Accept': 'application/json',
      Authorization: `bearer ${token}`
    }
  })
}

export const Request = axios.create({
  headers: {
    'Accept': 'application/json'
  }
})

const { proxy, clientID, clientSecret } = window.COMMENT
export const Verificate = () => Request.post(proxy, {
  code: window.auth.code,
  client_id: clientID,
  client_secret: clientSecret
})


export const Github = axios.create({
  baseURL: 'https://api.github.com',
  headers: {
    'Accept': 'application/json'
  }
})