import React, { useState } from 'react'
import { useHistory } from 'react-router-dom'
import { debounce } from 'debounce'
import Remote from '../utils/leancloud'
import Local from '../utils/local'

export default ({ id, comments }) => {
  const history = useHistory()
  const go = () => {
    window.isComment = true
    window.positionY = window.scrollY
    history.push(`${window.WEIBO.root}/${id}`)
    setTimeout(() => {
      window.scrollTo(0, 0)
    }, 0)
  }
  const [reactions, setReactions] = useState(Remote.get(id))
  const [liked, setLiked] = useState(reactions > 0 && Local.has(id))
  const [liking, setLiking] = useState(false)

  const like = () => {
    setLiked(true)
    setReactions(reactions + 1)
    Local.save(window.WEIBO.sendQueueKey, queue => (queue[id] = 1, queue))
    Local.save(id, 1)
    Remote.like(id)
  }
  const unlike = () => {
    setLiked(false)
    setReactions(reactions - 1)
    Local.remove(id)
    Remote.unlike(id)
  }
  const toggleLike = () => {
    setLiking(!liked)
    // liked ? unlike() : like()
    !liked && like()
  }
  return (
    <div className="is-issue-footer">
      {/* eslint-disable-next-line */}
      <a onClick={debounce(toggleLike, window.WEIBO.debounce)}
        className={`is-issue-icon is-issue-reaction ${liking ? 'is-issue-like-animation' : ''} ${liked ? 'is-issue-liked' : ''}`}>
        <div className="is-icon-reaction-wrap">
          <svg className="is-icon" aria-hidden="true">
            <use xlinkHref="#icon-like"></use>
          </svg>
        </div>
        <span className="is-reaction-count">
          {reactions}
        </span>
      </a>
      {/* eslint-disable-next-line */}
      <a onClick={go} className="is-issue-icon is-issue-comment">
        <div className="is-icon-comment-wrap">
          <svg className="is-icon" aria-hidden="true">
            <use xlinkHref="#icon-comment"></use>
          </svg>
        </div>
        <span className="is-comment-count">
          {comments}
        </span>
      </a>
    </div>
  )
}
