export default _url => {
  const windowArea = {
    width: window.COMMENT.width,
    height: window.COMMENT.height,
  }

  windowArea.left = Math.floor(window.screenX + ((window.outerWidth - windowArea.width) / 2))
  windowArea.top = Math.floor(window.screenY + ((window.outerHeight - windowArea.height) / 3))

  const sep = (_url.indexOf('?') !== -1) ? '&' : '?'
  const url = `${_url}${sep}`
  const windowOpts = `toolbar=0,scrollbars=1,status=1,resizable=1,location=1,menuBar=0,
    width=${windowArea.width},height=${windowArea.height},
    left=${windowArea.left},top=${windowArea.top}`

  const authWindow = window.open(url, 'Weibo OAuth Application', windowOpts)
  // Create IE + others compatible event handler
  const eventMethod = window.addEventListener ? 'addEventListener' : 'attachEvent'
  const eventer = window[eventMethod]
  const messageEvent = eventMethod === 'attachEvent' ? 'onmessage' : 'message'
  const handleMessage = (e, resolve, reject) => {
    authWindow.close()
    if (e.origin !== window.COMMENT.domain) {
      reject('Not allowed')
    }
    if(typeof e.data !== 'string'){
      return
    }
    const { token } = JSON.parse(e.data)

    if (!token) {
      reject('Unauthorised')
    }
    resolve(token)
  }

  return new Promise((resolve, reject) => {
    eventer(messageEvent, e => handleMessage(e, resolve, reject), false)
  })
}
