import React, { PureComponent } from 'react'
import '../style/about.less'
import { getLabels } from '../utils/graphql'
import { Weibo } from '../utils/http'
import Label from './Label'
import Session from '../utils/session'

export default class About extends PureComponent {
  state = {
    labels: null
  }
  componentDidMount() {
    // NOTE: polyfill for details & summary, 其实没必要
    // eslint-disable-next-line no-unused-expressions
    import('../style/about.js')
    if (Session.has('labels')) {
      const labels = Session.get('labels')
      this.setState({ labels })
      return
    }
    const { owner, repo } = window.WEIBO
    Weibo.post('/graphql', getLabels({ owner, repo })
    ).then(res => {
      const labels = res.data.data.repository.labels.nodes
      this.setState({ labels })
      Session.save('labels', labels)
    })
  }
  render() {
    const { labels } = this.state

    return (
      <div className="about-container collapse">
        <p className="about-title">Details &amp; Summary</p>
        <details>
          <summary><span role="img" aria-label="about-weibo">🎉</span>关于微博</summary>
          <div className="details-wrapper">
            <div className="details-styling">
              <p>在大一的时候，我就关掉了自己的所有动态。</p>
              <p>我从来没有发过<del>朋友圈</del>，新浪微博等国内平台。</p>
              <p>我不想自己的声音被监管，自己的数据被掌控，自己的行为被分析。</p>
              <p>我不想成为一个工具。</p>
              <p>我一直在寻找一个没有任何杂质的地方。</p>
              <p>它可能是一个情感树洞，一个技术社区，一个随时分享想法的平台。</p>
              <p>你眼前所看到的微博，正是我这一强烈需求的产物。</p>
              <p>它对我意义重大。</p>
            </div>
          </div>
        </details>
        <details>
          <summary><span role="img" aria-label="about-content">✨关于内容</span></summary>
          <div className="details-wrapper">
            <div className="details-styling">
              <p>目前，微博共有{Array.isArray(labels) ? labels.length : 14}个分类，具体如下：</p>
              <div>
                {Array.isArray(labels) && labels.map(({ name, color }, index) => (
                  <Label className={'labels-label'} name={name} color={color} key={index} />
                ))}
              </div>
            </div>
          </div>
        </details>
        <details>
          <summary><span role="img" aria-label="about-subscription">🔊关于订阅</span></summary>
          <div className="details-wrapper">
            <div className="details-styling">
              <ul>
                <li><code>Watch</code>&nbsp;<a href="https://github.com/Tomotoes/weibo" target="_blank" rel="noopener noreferrer">我的微博仓库</a></li>
                <li><code>Join</code>&nbsp;<a href="https://t.me/tomotoes" target="_blank" rel="noopener noreferrer">我的 TG 频道</a></li>
                <li><code>Subscribe</code>&nbsp;<a href="https://rsshub.app/github/issue/Tomotoes/weibo" target="_blank" rel="noopener noreferrer">我的 RSS 源</a></li>
              </ul>
            </div>
          </div>
        </details>
      </div>
    )
  }

}
