import React from 'react'
import { useHistory } from 'react-router-dom'

export default () => {
  const history = useHistory()
  const back = () => {
    history.goBack()
    setTimeout(() => {
      window.scrollTo(0, window.positionY)
      window.isComment = false
    }, 0)
  }
  return (
    <div className="is-issue-footer">
      {/* eslint-disable-next-line */}
      <a onClick={back}
        className=" is-issue-icon is-icon-back wobble-horizontal" >
        <svg className="is-icon" aria-hidden="true">
          <use xlinkHref="#icon-lvsefenkaicankaoxianban-"></use>
        </svg>
        <span className="is-back-text">
          {window.WEIBO.returnText}
        </span>
      </a >
    </div>
  )
}