/* eslint-disable camelcase */
/* eslint-disable jsx-a11y/anchor-is-valid */
import React, { PureComponent } from 'react'
import FlipMove from 'react-flip-move'
import autosize from 'autosize'
import './style/index.styl'
import 'react-awesome-button/dist/styles.css'

import Avatar from './component/Avatar'
import LoadMore from './component/Button/LoadMore'
import Login from './component/Button/Login'
import Preview from './component/Button/Preview'
import Edit from './component/Button/Edit'
import CommentButton from './component/Button/Comment'
import Update from './component/Button/Update'
import Cancel from './component/Button/Cancel'
import CommentComponent from './component/Comment'
import Svg from './component/Svg'
import Local from '../../utils/local'
import Session from '../../utils/session'

import {
  queryStringify,
  formatErrorMsg,
  filterComments,
  filterComment,
  getIssueUrl,
  getCommentUrl
} from '../../utils'
import { Github, RequestByToken, Comment } from '../../utils/http'
import Window from '../../utils/window'
import { getComments, addComment, updateComment, unlike, like } from '../../utils/graphql'

export default class GitalkComponent extends PureComponent {
  state = {
    user: null,
    comments: [],
    comment: '',
    page: 1,
    hasNextPage: false,
    cursor: null,
    issueUrl: getIssueUrl(this.props.number),
    commentUrl: getCommentUrl(this.props.number),
    isIniting: true,
    isUpdating: false,
    uId: 0,
    isPreview: false,
    previewHtml: '',
    isOccurError: false,
    errorMsg: '',
  }
  get accessToken() {
    return this._accessToken || Local.get('token')
  }
  set accessToken(token) {
    Local.save('token', token)
    this._accessToken = token
  }
  componentDidUpdate() {
    this.commentEL && autosize(this.commentEL)
  }
  componentDidMount() {
    this.init()
      .then(() => this.setState({ isIniting: false }))
      .catch(this.handleError)
  }
  init() {
    if (this.props.comments === 0) {
      if (this.accessToken) {
        return this.getUserInfo()
      }
      return Promise.resolve()
    }
    if (this.accessToken) {
      return Promise.all([this.getComments(), this.getUserInfo()])
    }
    return this.getComments()
  }
  getComments() {
    const { owner, repo, pageSize } = window.COMMENT
    const { cursor, comments } = this.state
    const { number } = this.props
    return Comment.post(
      '/graphql',
      getComments({ owner, repo, id: number, pageSize, cursor })
    ).then(res => {
      const data = res.data.data.repository.issue.comments
      const { hasNextPage, endCursor: cursor } = data.pageInfo
      comments.push(...filterComments(data.nodes))
      this.setState({ comments, hasNextPage, cursor })
    }).catch(this.handleError)
  }
  getUserInfo() {
    if (Session.has('user')) {
      const user = Session.get('user')
      return Promise.resolve(this.setState({ user }))
    }
    return Github.get('/user', {
      headers: { Authorization: `token ${this.accessToken}` }
    }).then(res => {
      const { login, avatar_url } = res.data
      const user = { login, avatar: avatar_url }
      this.setState({ user })
      Session.save('user', user)
    }).catch(this.logout)
  }
  createComment = next => {
    if (!this.state.comment.length) {
      this.commentEL.focus()
      return
    }
    const { comment, comments, hasNextPage } = this.state
    const { addComments, number, gId } = this.props

    RequestByToken(this.accessToken)
      .post('/graphql', addComment(gId, comment))
      .then(res => {
        if (hasNextPage && comments.length) {
          this.setState({ comment: '' })
        } else {
          const newComment = filterComment(
            res.data.data.addComment.commentEdge.node)
          comments.push(newComment)
          this.setState({ comment: '', comments })
        }
        addComments(number)
      }).then(next).catch(error => {
        next(false, window.COMMENT.comment.fail)
        this.handleError(error)
      })
  }
  updateComment = next => {
    if (!this.state.comment.length) {
      this.commentEL.focus()
      return
    }
    const { comment, comments, uId } = this.state
    RequestByToken(this.accessToken)
      .post('/graphql', updateComment(uId, comment))
      .then(res => {
        const newComment = filterComment(
          res.data.data.updateIssueComment.issueComment)
        this.setState({
          comment: '',
          comments: comments.map(c => c.gId === uId ? newComment : c),
          isUpdating: false
        }, next)
      }).catch(_ => {
        next(false, window.COMMENT.update.fail)
      })
  }
  previewComment = next => {
    this.setState({ isPreview: true, previewHtml: window.COMMENT.preview.html }, () => {
      Github.post('/markdown', { text: this.state.comment }, {
        headers: this.accessToken && { Authorization: `token ${this.accessToken}` }
      }).then(res => {
        this.setState({ previewHtml: res.data }, next)
      }).catch(error => {
        this.handleError(error, {})
        next(false, window.COMMENT.preview.fail)
      })
    })
  }
  getMoreComments = next => {
    this.getComments().then(next).catch(() => next(false, window.COMMENT.loadFail))
  }
  like = comment => {
    RequestByToken(this.accessToken)
      .post('/graphql', like(comment.gId))
  }
  unlike = comment => {
    RequestByToken(this.accessToken)
      .post('/graphql', unlike(comment.gId))
  }
  login = next => {
    const githubOauthUrl = 'https://github.com/login/oauth/authorize'
    const query = {
      client_id: window.COMMENT.clientID,
      redirect_uri: window.location.href,
      scope: 'public_repo'
    }
    const loginLink = `${githubOauthUrl}?${queryStringify(query)}`
    Window(loginLink).then(token => {
      this.accessToken = token
      this.getUserInfo().then(next).catch(error => {
        next(false, window.COMMENT.login.fail)
        this.handleError(error)
      })
    }).catch(error => {
      next(false, window.COMMENT.login.fail)
      this.handleError(error)
    })
  }
  logout = () => {
    this.setState({ user: null })
    Local.remove('token')
    Session.remove('user')
  }
  handleError = (error, state = { isIniting: false }) => {
    console.log('err:', error)
    this.setState({
      ...state,
      isOccurError: true,
      errorMsg: formatErrorMsg(error)
    })
  }
  reply = replyComment => () => {
    const { comment } = this.state
    const replyCommentBody = replyComment.body
    let replyCommentArray = replyCommentBody.split('\n')
    replyCommentArray.unshift(`@${replyComment.user.login}`)
    replyCommentArray = replyCommentArray.map(t => `> ${t}`)
    replyCommentArray.push('')
    replyCommentArray.push('')
    if (comment) { replyCommentArray.unshift('') }
    this.setState({ comment: comment + replyCommentArray.join('\n') }, () => {
      autosize.update(this.commentEL)
      this.commentEL.focus()
    })
  }
  edit = editComment => () => {
    this.setState({
      comment: editComment.body,
      isUpdating: true,
      uId: editComment.gId
    }, () => {
      autosize.update(this.commentEL)
      this.commentEL.focus()
    })
  }
  changeComment = e => this.setState({ comment: e.target.value })

  initing() {
    return <div className="gt-initing">
      <span className="square-loader"><span className="square-inner"></span></span>
      <p className="is-initing-text">{window.COMMENT.initText}</p>
    </div>
  }
  meta() {
    const { user, issueUrl } = this.state
    const { comments } = this.props

    return (
      <div className="gt-meta" key="meta" >
        <span className="gt-counts" >
          <a className="gt-link gt-link-counts" href={issueUrl} rel="noopener noreferrer" target="_blank">
            {comments} </a>{window.COMMENT.counts}
        </span>
        <div className="gt-user">
          {user &&
            <div className="gt-user-inner">
              <span className="gt-user-name">{user.login}&nbsp;|&nbsp;</span>
              <a className="gt-action gt-action-logout" onClick={this.logout} >
                <span className="gt-action-text">{window.COMMENT.logout}</span>
              </a>
              &nbsp;
            </div>
          }
        </div>
      </div >
    )
  }
  header() {
    const { user, comment, previewHtml, isPreview, isUpdating } = this.state
    return (
      <div className="gt-header" key="header">
        {user ?
          <Avatar className="gt-header-avatar" src={user.avatar} alt={user.login} /> :
          <a className="gt-avatar-github" onClick={this.login}>
            <Svg className="gt-ico-github" name="github" />
          </a>
        }
        <div className="gt-header-comment">
          <textarea ref={t => { this.commentEL = t }}
            className={`gt-header-textarea ${isPreview ? 'hide' : ''}`}
            value={comment} onChange={this.changeComment} placeholder={window.COMMENT.placeholder} />
          <div className={`gt-header-preview markdown-body ${isPreview ? '' : 'hide'}`}
            dangerouslySetInnerHTML={{ __html: previewHtml }} />
          <div className="gt-header-controls">
            <a className="gt-header-controls-tip" href="https://guides.github.com/features/mastering-markdown/" rel="noopener noreferrer" target="_blank">
              <Svg className="gt-ico-tip" name="tip" text={window.COMMENT.supportMD} />
            </a>
            {user && (isUpdating ? <Update disabled={comment.length === 0} onClick={this.updateComment} /> :
              <CommentButton disabled={comment.length === 0} onClick={this.createComment} />)}
            {isUpdating ? <Cancel onClick={next => this.setState({ isUpdating: false, comment: '' }, next)} /> : (
              !isPreview ?
                <Preview disabled={comment.length === 0} onClick={this.previewComment} /> :
                <Edit onClick={next => this.setState({ isPreview: false }, next)} />)}
            {!user && <Login onClick={this.login} />}
          </div>
        </div>
      </div>
    )
  }
  comments() {
    const { user, comments, hasNextPage } = this.state
    const { flipMoveOptions } = window.COMMENT
    return (
      <div className="gt-comments" key="comments">
        <FlipMove {...flipMoveOptions}>
          {comments.map((c, idx) => (
            <CommentComponent comment={c} key={idx} user={user} reply={this.reply(c)} edit={this.edit(c)}
              like={this.like.bind(this, c)} unlike={this.unlike.bind(this, c)} />
          ))}
        </FlipMove>
        {!comments.length && <p className="gt-comments-null">{window.COMMENT.firstCommentText}</p>}
        {(hasNextPage && comments.length) && <div className="gt-comments-controls">
          <LoadMore onClick={this.getMoreComments} />
        </div>}
      </div>
    )
  }

  render() {
    const { isIniting, isOccurError, errorMsg } = this.state
    return (
      <div className="gt-container">
        {isIniting && this.initing()}
        {!isIniting && this.meta()}
        {isOccurError && <div className="gt-error"> {errorMsg} </div>}
        {!isIniting && [this.header(), this.comments()]}
      </div>
    )
  }
}
