import React from 'react'
import FlipMove from 'react-flip-move'
import Issue from './Issue'
import Egg from './Egg'
import About from './About'

export default ({ issues, isHome, isLoading, hasNextPage, anchor, isOccurError, errorMsg }) => {
  const { animation, loading } = window.WEIBO
  return (
    <div className='is-container'>
      {issues.length > 0 && isHome && <About />}
      {issues.length > 0 && (
        <div className="is-issues" key="issues">
          <FlipMove {...animation}>
            {issues.map((issue, index) => <Issue issue={issue} key={index} isHome={isHome}
              {...(index + 1 === issues.length && { forwardedRef: anchor })}
            />)}
          </FlipMove >
        </div>)}
      {isLoading &&
        (<div className="is-initing">
          <span className="square-loader"><span className="square-inner"></span></span>
          <p className="is-initing-text">{loading}</p>
        </div>)}
      {isOccurError && <div className="is-error"> {errorMsg} </div>}
      {!hasNextPage && window.WEIBO.egg.enable && <Egg />}
    </div >
  )
}
