import React, { PureComponent as Component } from 'react'
import {
  MemoryRouter as Router,
  Switch,
  Route,
} from 'react-router-dom'
import Home from './component/Home'
import Comment from './component/Comment'
import './style/index.less'
import { Weibo } from './utils/http'
import { getIssues } from './utils/graphql'
import { throttle } from './utils/func'
import Remote from './utils/leancloud'
import Session from './utils/session'
import { formatErrorMsg, filterIssues } from './utils'

export default class App extends Component {

  state = {
    issues: [],
    cursor: null,
    isLoading: true,
    hasNextPage: true,
    isOccurError: false,
    errorMsg: '',
    isHome: true,
    anchor: React.createRef()
  }

  componentDidMount() {
    if (Session.has('state')) {
      const state = Session.get('state')
      const { issues, hasNextPage, cursor } = state
      Remote.init().then(() => {
        this.setState({ issues, hasNextPage, cursor, isLoading: false })
      })
      hasNextPage && window.addEventListener('scroll', this.lazyLoad, false)
      return
    }
    this.getIssues(() => Promise.all([this.fetch(), Remote.init()]))
    window.addEventListener('scroll', this.lazyLoad, false)
  }

  fetch = () => {
    const { cursor } = this.state
    const { owner, repo, pageSize } = window.WEIBO
    return Weibo.post(
      '/graphql',
      getIssues({ owner, repo, cursor, pageSize })
    )
  }

  getIssues = (fetch = this.fetch) => {
    const { issues } = this.state
    return fetch().then(res => {
      res = res.length ? res[0] : res
      const data = res.data.data.repository.issues
      const { hasNextPage, endCursor: cursor } = data.pageInfo
      if (!hasNextPage) {
        window.removeEventListener('scroll', this.lazyLoad)
      }
      issues.push(...filterIssues(data.nodes))
      this.setState({ hasNextPage, cursor, issues, isLoading: false, isOccurError: false })
      Session.save('state', { hasNextPage, cursor, issues })
    }).catch(this.handleError)
  }

  addComments = number => {
    let { issues, cursor, hasNextPage } = this.state
    issues = this.state.issues.map(_ => (_.comments += (number == _.number), _))
    this.setState({ issues })
    Session.save('state', { issues, cursor, hasNextPage })
  }

  lazyLoad = throttle(() => {
    if (this.state.isLoading || window.isComment) {
      return
    }
    const clientHeight = window.innerHeight || document.documentElement.clientHeight
    if (window.scrollY + window.WEIBO.offsetTop * clientHeight < this.state.anchor.current.offsetTop) {
      return
    }
    this.setState({ isLoading: true }, this.getIssues)
  }, window.WEIBO.throttle)


  handleError = err => {
    console.error('err:', err)
    this.setState({
      isLoading: false,
      isOccurError: true,
      errorMsg: formatErrorMsg(err)
    })
  }

  render() {
    return (
      < Router initialEntries={[window.WEIBO.root]} >
        <Switch>
          <Route path={window.WEIBO.root} exact>
            <Home {...this.state} getIssues={this.getIssues} />
          </Route>
          <Route path={`${window.WEIBO.root}/:id`}>
            <Comment issues={this.state.issues} addComments={this.addComments} />
          </Route>
        </Switch>
      </Router >
    )
  }
}
