export const getIssues = vars => {
  const ql = `
  query getIssues($owner: String!, $repo: String!, $cursor: String, $pageSize: Int!) {
    repository(owner: $owner, name: $repo) {
      issues(first: $pageSize, after: $cursor, orderBy: {field: CREATED_AT, direction: DESC}, filterBy: {createdBy: $owner, states: OPEN}) {
        pageInfo {
          hasNextPage
          endCursor
        }
        nodes {
          id
          number
          createdAt
          bodyHTML
          comments(first: 1) {
            totalCount
          }
          labels(first: 1) {
            nodes {
              name
              color
            }
          }
        }
      }
    }
  }
  `
  if (vars.cursor === null) delete vars.cursor

  return {
    operationName: 'getIssues',
    query: ql,
    variables: vars
  }
}

export const getLabels = vars => {
  const ql = `
  query getLabels($owner: String!, $repo: String!) {
    repository(owner: $owner, name: $repo) {
      labels(first:100){
        nodes {
          name
          color
        }
      }
    }
  }
  `
  return {
    operationName: 'getLabels',
    query: ql,
    variables: vars
  }
}

export const getComments = vars => {
  const ql = `
  query getIssueAndComments($owner: String!, $repo: String!, $id: Int!, $cursor: String, $pageSize: Int!) {
    repository(owner: $owner, name: $repo) {
      issue(number: $id) {
        comments(first: $pageSize, after: $cursor) {
          pageInfo {
            hasNextPage
            endCursor
          }
          nodes {
            id
            databaseId
            author {
              avatarUrl
              login
            }
            bodyHTML
            body
            createdAt
            reactions(first: 100, content: HEART) {
              totalCount
              pageInfo {
                hasNextPage
              }
              nodes {
                user {
                  login
                }
              }
            }
          }
        }
      }
    }
  }  
  `

  if (vars.cursor === null) delete vars.cursor

  return {
    operationName: 'getIssueAndComments',
    query: ql,
    variables: vars
  }
}

export const addComment = (id, body) => ({
  operationName: 'AddComment',
  query: `
  mutation AddComment {
    addComment(input: {subjectId: "${id}", body: "${body}"}) {
      commentEdge {
        node {
          id
          databaseId
          author {
            avatarUrl
            login
          }
          bodyHTML
          body
          createdAt
          reactions(first: 100, content: HEART) {
            totalCount
            pageInfo {
              hasNextPage
            }
            nodes {
              user {
                login
              }
            }
          }
        }
      }
    }
  }
    
  `
})

export const updateComment = (id, body) => ({
  operationName: 'UpdateIssueComment',
  query: `
  mutation UpdateIssueComment {
    updateIssueComment(input: {id: "${id}", body: "${body}"}) {
      issueComment {
        id
        databaseId
        author {
          avatarUrl
          login
        }
        bodyHTML
        body
        createdAt
        reactions(first: 100, content: HEART) {
          totalCount
          pageInfo {
            hasNextPage
          }
          nodes {
            user {
              login
            }
          }
        }
      }
    }
  }
  `
})

export const unlike = id => ({
  operationName: 'RemoveReaction',
  query: `
  mutation RemoveReaction {
    removeReaction(input: {subjectId: "${id}", content: HEART}) {
      reaction {
        content
      }
    }
  }
  `
})

export const like = id => ({
  operationName: 'AddReaction',
  query: `
  mutation AddReaction {
    addReaction(input: {subjectId: "${id}", content: HEART}) {
      reaction {
        user {
          login
        }
      }
    }
  }
  `
})