import Local from './local'

export default class Remote {
  static reactions = null
  static Reaction = null
  static id = window.LEANCLOUD.id
  static table = window.LEANCLOUD.table
  static column = window.LEANCLOUD.column
  static defaultValue = 0

  static async init() {
    const Reaction = new window.AV.Query(Remote.table)
    try {
      Remote.Reaction = await Reaction.get(Remote.id)
      Remote.reactions = Remote.Reaction.get(Remote.column)

      // NOTE: 之前未发送的点赞消息数据
      const sendQueue = Local.get(window.WEIBO.sendQueueKey)
      if (!sendQueue) {
        Local.save(window.WEIBO.sendQueueKey, {})
        return
      }

      const notSendIdList = Object.keys(sendQueue).filter(id => sendQueue[id])
      Remote.done(...notSendIdList)
    } catch (error) {
      console.error(error)
    }
  }

  static get(id) {
    if (Remote.reactions && Remote.reactions[id]) {
      return Remote.reactions[id]
    }
    return Remote.defaultValue
  }

  static like(id) {
    Remote.done(id)
  }
  static unlike(id) {
    if (!Remote.reactions || !Remote.reactions[id] || Remote.reactions[id] < 0) {
      return
    }
    Remote.reactions[id] -= 1
    Remote.done(id)
  }
  static done(...idList) {
    if (!Remote.reactions || !idList.length) { return }
    idList.forEach(id => {
      if (Remote.reactions[id]) {
        Remote.reactions[id] += 1
      } else {
        Remote.reactions[id] = 1
      }
    })

    Remote.Reaction.set(Remote.column, Remote.reactions)
      .save()
      .then(() => {
        Local.save(window.WEIBO.sendQueueKey, queue => idList.reduce((q, id) => (q[id] = 0, q), queue))
      })
      .catch(console.error)
  }
}
