import React from 'react'
import { AwesomeButtonProgress, AwesomeButton } from 'react-awesome-button'

export default ({ onClick, disabled }) => {
  const { loading, success, text } = window.COMMENT.update
  if (disabled) {
    return <AwesomeButton type="secondary" size="medium" > {text} </AwesomeButton>
  }
  return <AwesomeButtonProgress
    type="secondary" size="medium"
    loadingLabel={loading} resultLabel={success}
    action={(_, next) => { onClick(next) }}
  >
    {text}
  </AwesomeButtonProgress>
}
