import React from 'react'
import { AwesomeButtonProgress } from 'react-awesome-button'

export default ({ onClick }) => {
  const { loading, success, text } = window.COMMENT.loadMore
  return <AwesomeButtonProgress
    type="secondary" size="medium" 
    loadingLabel={loading} resultLabel={success}
    action={(_, next) => { onClick(next) }}
  >
    {text}
  </AwesomeButtonProgress>
}

