import React from 'react'

export default () => {
  const { text1, text2, button, tip1, tip2 } = window.WEIBO.egg
  const egg = () => {
    let s = document.createElement('script') 
    document.body.appendChild(s) 
    s.src = '//hi.kickassapp.com/kickass.js'
    alert(tip1)
    alert(tip2)
  }
  return (
    <div className="is-issue-end">
      <h1 className="is-end-text">{text1}</h1>
      <h1 className="is-end-text">{text2}</h1>
      <button className="is-end-button" onClick={egg}>
        <h1 className="is-end-button-text">{button}</h1>
      </button>
    </div>
  )
}