/* eslint-disable jsx-a11y/anchor-is-valid */
import React, { PureComponent } from 'react'
import Svg from './Svg'

export default class Like extends PureComponent {
  state = {
    liked: false,
    count: 0
  }
  componentDidMount() {
    const { reactions, currentUser } = this.props
    let count = reactions.totalCount

    const liked = currentUser &&
      reactions.nodes.some(({ user: { login } }) => login === currentUser)
    if (count === 100 && reactions.pageInfo && reactions.pageInfo.hasNextPage) {
      count = '100+'
    }
    this.setState({ count, liked })
  }
  toggleLike = () => {
    if (!this.props.currentUser) {
      return
    }
    let { count, liked } = this.state
    const { unlike, like } = this.props

    if (count !== '100+') {
      count += liked ? -1 : 1
    }
    this.setState({ count, liked: !liked })
    liked ? unlike() : like()
  }

  render() {
    const { count, liked } = this.state
    return (
      <a className="gt-comment-like" title="喜欢" onClick={this.toggleLike} >
        {
          liked ? (
            <Svg className="gt-ico-heart" name="heart_on" text={count} />) :
            (<Svg className="gt-ico-heart" name="heart" text={count} />)
        }
      </a >
    )
  }

}
