import React from 'react'
import { useParams } from 'react-router-dom'
import Home from './Home'
import GitalkComponent from './Gitalk/Gitalk'
import './Gitalk/style/gitalk.less'

export default ({ issues, addComments }) => {
  if (!issues || !issues.length) {
    window.location.href = window.WEIBO.root
  }
  const { id } = useParams()
  const current = issues.filter(issue => issue.number == id)
  const state = {
    issues: current,
    isLoading: false,
    isOccurError: false,
    hasNextPage: true,
    isHome: false
  }
  
  const { number, comments, gId } = current[0]
  return (
    <div>
      <Home {...state} />
      <GitalkComponent number={number} gId={gId} comments={comments} addComments={addComments} />
    </div>
  )
}
