export default class Local {
  static namespace = 'WEIBO'

  static save(id, item) {
    if (typeof item === 'function') {
      const object = Local.get(id)
      localStorage.setItem(`${Local.namespace}-${id}`, JSON.stringify(item(object)))
      return
    }
    localStorage.setItem(`${Local.namespace}-${id}`, JSON.stringify(item))
  }
  static remove(id) {
    localStorage.removeItem(`${Local.namespace}-${id}`)
  }
  static get(id) {
    return JSON.parse(localStorage.getItem(`${Local.namespace}-${id}`))
  }
  static has(id) {
    const item = localStorage.getItem(`${Local.namespace}-${id}`)
    return !!item && item !== 'null' && item !== null
  }
}
