import React from 'react'
import { ReactComponent as Edit } from '../icon/edit.svg'
import { ReactComponent as Github } from '../icon/github.svg'
import { ReactComponent as HeartOn } from '../icon/heart_on.svg'
import { ReactComponent as Heart } from '../icon/heart.svg'
import { ReactComponent as Reply } from '../icon/reply.svg'
import { ReactComponent as Tip } from '../icon/tip.svg'

const getSvg = name => {
  if (name === 'edit') {
    return <Edit />
  }
  if (name === 'github') {
    return <Github />
  }
  if (name === 'heart_on') {
    return <HeartOn />
  }
  if (name === 'heart') {
    return <Heart />
  }
  if (name === 'reply') {
    return <Reply />
  }
  if (name === 'tip') {
    return <Tip />
  }
}

export default ({ className, text, name }) => (
  <span className={`gt-ico ${className}`}>
    <span className="gt-svg">{getSvg(name)}</span>
    {
      <span className="gt-ico-text">{text}</span>
    }
  </span>
)
