/* eslint-disable jsx-a11y/anchor-is-valid */
import React, { PureComponent } from 'react'
import Avatar from './Avatar'
import Svg from './Svg'
import Like from './Like'
import { filterDate } from '../../../utils'

export default class Comment extends PureComponent {
  componentDidMount() {
    const commentEl = this.node
    const emailResponse = commentEl.querySelector('.email-hidden-toggle>a')
    if (emailResponse) {
      emailResponse.addEventListener('click', e => {
        e.preventDefault()
        commentEl.querySelector('.email-hidden-reply').classList.toggle('expanded')
      }, true)
    }
  }
  render() {
    const { comment, user, reply, unlike, like, edit } = this.props
    let currentUser = false
    if (user) {
      currentUser = user.login
    }
    const commentUser = comment.user.login

    const enableEdit = currentUser && commentUser === currentUser
    const isAdmin = window.COMMENT.admin.some(name => name === commentUser)

    return (
      <div ref={node => { this.node = node }} className={`gt-comment ${isAdmin ? 'gt-comment-admin' : ''}`}>
        <Avatar className="gt-comment-avatar"
          src={comment.user && comment.user.avatar}
          alt={comment.user && comment.user.login} />

        <div className="gt-comment-content">
          <div className="gt-comment-header">
            <div className={`gt-comment-block-${user ? '2' : '1'}`} />
            <a className="gt-comment-username" href={comment.user && `https://github.com/${comment.user.login}`} target="_blank" rel="noopener noreferrer">
              {comment.user && comment.user.login}
            </a>
            <span className="gt-comment-text">{window.COMMENT.commented}</span>
            <span className="gt-comment-date">
              {filterDate(comment.createdAt)}
            </span>

            <Like like={like} unlike={unlike} currentUser={currentUser} reactions={comment.reactions} />

            {currentUser && (enableEdit ? (
              <a className="gt-comment-edit" title="编辑" onClick={edit}>
                <Svg className="gt-ico-edit" name="edit" />
              </a>
            ) : (
                <a className="gt-comment-reply" title="回复" onClick={reply}>
                  <Svg className="gt-ico-reply" name="reply" />
                </a>
              ))}
          </div>
          <div className="gt-comment-body markdown-body"
            dangerouslySetInnerHTML={{ __html: comment.bodyHtml }} />
        </div>
      </div>
    )
  }


}
