export default class Session {
  static compatible = !!window.sessionStorage
  static save(id, item) {
    if (!Session.compatible) {
      return null
    }
    sessionStorage.setItem(`WEIBO-${id}`, JSON.stringify(item))
  }
  static remove(id) {
    if (!Session.compatible) {
      return null
    }
    sessionStorage.removeItem(`WEIBO-${id}`)
  }
  static get(id) {
    if (!Session.compatible) {
      return null
    }
    return JSON.parse(sessionStorage.getItem(`WEIBO-${id}`))
  }
  static has(id) {
    if (!Session.compatible) {
      return null
    }
    const item = sessionStorage.getItem(`WEIBO-${id}`)
    return !!item && item !== 'null' && item !== null
  }
}